import * as React from 'react'
import Layout from '../components/layout'
import {Row, Col, FormControl} from 'react-bootstrap'
import {useState} from 'react'
import OverlayBox from '../components/overlay-box'
import Clipboard from 'react-clipboard.js'
import {Base64} from 'js-base64'
import pako from 'pako'

const Create = ({location}) => {
  const [text, setText] = useState('')
  const url = `${location.protocol}//${location.host}/${encode(text)}`
  return (
    <Layout isMain brand={(
      <p>We create a link for your text, so that everyone can copy the text to their clipboard super easily!</p>
    )}>
      <Row>
        <Col>
          <FormControl as="textarea" rows={5} style={{resize: 'none'}} onChange={e => setText(e.target.value)}/>
        </Col>
      </Row>
      <Row className="mt-4">
        <Col>
          <OverlayBox text="copied!" className="text-dark" overlaidClassName="overlaid" render={({show}) => (
            <Clipboard id="copy" component="div" className={`btn btn-primary ${text ? '' : 'disabled'}`}
                       data-clipboard-text={url}
                       onSuccess={show} onError={() => alert('failed to copy!')}>
              <div role={text ? '' : 'button'}>Create a Link!</div>
            </Clipboard>
          )}/>
        </Col>
      </Row>
    </Layout>
  )
}

function encode(text) {
  try {
    const bin = new TextEncoder().encode(text)
    const zip = pako.deflate(bin, {level: 9})
    return Base64.fromUint8Array(zip, true)
  } catch (e) {
    console.log(e)
  }
}

export default Create
